<template>
	<view class="content">
		<view v-if="show==1">
			<u-row>
				<u-col span="12" style="text-align: center;min-height: 104rpx;padding-top: 24rpx;">
					<view class="topBox" v-show="search==1">全民反恐
						<image class="ss" src="/static/img/sous.png" @click="search=2"></image>
					</view>
					<view v-show="search==2"><u-search shape="round" v-model="keyword"></u-search></view>
				</u-col>
				<u-col span="12" style="text-align: center;">
					<image style="border-radius: 18rpx;" src="/static/img/banner.png" mode="widthFix"></image>
				</u-col>
			</u-row>
			<u-grid :col="4" :border="false">
				<u-grid-item bg-color="transparent" @click="topage(0)">
					<image src="/static/img/zyzc.png" mode="widthFix" style="width: 84rpx;"></image>
					<view class="grid-text">志愿者注册</view>
				</u-grid-item>
				<u-grid-item bg-color="transparent" @click="topage(1)">
					<image src="/static/img/xssb.png" mode="widthFix" style="width: 84rpx;"></image>
					<view class="grid-text">线索上报</view>
				</u-grid-item>
				<u-grid-item bg-color="transparent" @click="topage(2)">
					<image src="/static/img/fkzx.png" mode="widthFix" style="width: 84rpx;"></image>
					<view class="grid-text">反恐资讯</view>
				</u-grid-item>
				<u-grid-item bg-color="transparent" @click="topage(3)">
					<image src="/static/img/hddt.png" mode="widthFix" style="width: 84rpx;"></image>
					<view class="grid-text">活动动态</view>
				</u-grid-item>
			</u-grid>
			<u-tabs-swiper ref="uTabs" bg-color="transparent" :list="list" :current="current"
				@change="tabsChange"></u-tabs-swiper>
			<swiper :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 500rpx;">
						<view class="list01" v-for="(item,index) in zxlist" :key="index" @click="details(item.id)" v-show="zxlist[0]">
							<u-row>
								<u-col span="8">
									<view class="blueline"></view>
									<span>{{item.universalAntiSeekAdviceMain97}}</span>
									<p>{{item.createTime}}</p>
								</u-col>
								<u-col span="4" style="text-align: center;">
									<image :src="item.file" mode="center" class="listimg"></image>
								</u-col>
							</u-row>
						</view>
						<view style="width: 100%;text-align: center;color: #999;font-size: 36rpx;margin-top: 140rpx;" v-show="!zxlist[0]">暂无资讯</view>
					</scroll-view>
				</swiper-item>
				<swiper-item class="swiper-item">
					<scroll-view scroll-y style="height: 500rpx;">
						<view class="list02" v-for="(item,index) in hdlist" :key="index" v-show="hdlist[0]">
							<u-row>
								<u-col span="2">
									<u-avatar :src="item.header" size="90"></u-avatar>
								</u-col>
								<u-col span="10">
									<p class="titlebox">{{item.pushName}}</p>
									<p class="pbox">{{item.createTime}}</p>
								</u-col>
								<u-col span="12">
									<p class="pbox01 gray">{{item.content}}</p>
								</u-col>
								<u-col v-show="k" span="6" v-for="(k,m) in item.file.split(',')" :key="m"
									style="text-align:center;border-bottom: 1px solid #ECECEC;padding: 10rpx 0 24rpx 0;">
									<image :src="k" mode="widthFix" style="width: 233rpx;"></image>
								</u-col>
								<u-col span="12" style="margin-top: 20rpx;">
									<u-tag :text="i" mode="dark" v-for="(i,indx) in item.aboutList" :key="indx" />
								</u-col>
							</u-row>
						</view>
						<view style="width: 100%;text-align: center;color: #999;font-size: 36rpx;margin-top: 140rpx;" v-show="!hdlist[0]">暂无动态</view>
					</scroll-view>
				</swiper-item>
			</swiper>

		</view>
		<view v-if="show==2" style="padding-top: 200rpx;">
			<text class="title">全民反恐</text>
			<p class="smtitle">选择一个身份</p>
			<u-row>
				<u-col span="6">
					<view class="pt">
						普通注册
					</view>
				</u-col>
				<u-col span="6">
					<view class="pt zyz">
						志愿者注册
					</view>
				</u-col>
			</u-row>
		</view>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		listObj,
		antilistObj,
		getDicts
	} from '../../api/index.js'
	export default {
		props: {
			mytype: {
				type: String,
				default: '0'
			},
		},
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 3
				},
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '反恐资讯'
				}, {
					name: '活动动态'
				}],
				list01: [{
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, ],
				zxlist: [],
				hdlist: [],
				statusArr:[],
			}
		},
		onLoad() {
			getDicts('work_enter_status').then(res => { //系统是否
				if (res.code == 200) {
					this.statusArr = res.data;
				}
			})
			this.listArr();
		},
		methods: {
			listArr(){
				listObj(this.params).then(res => {
					if (res.code == 200) {
						this.zxlist = res.rows;
					}
				})
			},
			details(id){
				uni.navigateTo({
					url: '../news/details?id='+id
				})
			},
			fystatus(name) {
				let result = null;
				this.statusArr.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			topage(index) {
				let link = '';
				if (index == 0) {
					if(this.mytype=="2"){
						this.$refs.uToast.show({
							title: '正在审批中'
						});
						return;
					}else link = '../login/register';
				} else if (index == 1) {
					link = '../report/report';
				} else if (index == 2) {
					link = '../news/list';
				} else {
					link = '../Ordinary/dynamic';
				}
				uni.navigateTo({
					url: link
				})
			},
			tabsChange(index) {
				this.swiperCurrent = index;
				if (index == 1) {
					antilistObj(this.params).then(res => {
						if (res.code == 200) {
							this.hdlist = res.rows;
						}
					})
				}else{
					this.listArr();
				}
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background: url('@/static/img/bg.png') no-repeat center center fixed;
		background-size: cover;
		box-sizing: border-box;
		padding: 0 40rpx;
	}

	/deep/ uni-swiper {
		min-height: 570rpx;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		height: 172rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 30rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list01 p,
	.list02 p {
		margin-top: 20rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>